.product-details-full-action {
	display: none;

	@media (min-width: $screen-sm-min){
		display: block;
	}
}
.product-details-full-main-bottom-banner {
	margin-bottom: $sc-margin-lv4;
}

.product-details-full-rating-header {
	.global-views-star-rating-area {
		margin-right: $sc-margin-lv1;
	}
	.global-views-star-rating i {
		font-size: 20px;
	}
}


.product-details-full-main-content-left {
	@extend .col-xs-12;
    @extend .col-md-6;
    @extend .col-lg-offset-1;
	float:left;
	min-height:1px;
}

.product-details-full-main-content-right {
	@extend .col-xs-12;
    @extend .col-md-6;
    @extend .col-lg-5;
	min-height:1px;
	float:right;
	clear:right;
}

.product-details-full-actions-addtowishlist {
    width: 100%;
    display: inline-block;
    position: static;
    padding-left: $sc-padding-lv2;
    padding-right: $sc-padding-lv2;
    @media (min-width: $screen-sm-min) {
        width: 50%;
    }
    @media (min-width: $screen-lg-min) {
        width: 35%;
    }
}

